import React from "react";
import "./App.css";
import Select from "react-select";
import axios from "axios";
import { ActionMeta, OnChangeValue } from "react-select";
import CreatableSelect from "react-select/creatable";
import Button from "react-bootstrap/Button";
import "bootstrap/dist/css/bootstrap.min.css";

function App() {
  const [selectedOption, setSelectedOption] = React.useState({
    value: "LONG",
    label: "BUY",
  });
  const [selectedSize, setSelectedSize] = React.useState({value: '0', label:'0'});
  const [selectedSymbol, setSelectedSymbol] = React.useState({value: null, label: null});
  const options = [
    {
      value: "LONG",
      label: "BUY",
    },
    {
      value: "SHORT",
      label: "SELL",
    },
  ];
  const sizeOptions = [];
  const symbolOptions = [];
  const debug = () => {
    console.log(selectedOption, selectedSize, selectedSymbol);
  };
  const handleChange = (e) => {
    setSelectedOption(e);
  };
  const handleChangeSize = (newValue, actionMeta) => {
    console.group("Value Changed");
    console.log(newValue);
    setSelectedSize(newValue);
    console.log(`action: ${actionMeta.action}`);
    console.groupEnd();
  };
  const handleChangeSymbol = (newValue, actionMeta) => {
    console.group("Value Changed");
    console.log(newValue);
    setSelectedSymbol(newValue);
    console.log(`action: ${actionMeta.action}`);
    console.groupEnd();
  };

  const submitFunction = () => {
    const data = {
      action: (selectedOption as any).value,
      size: (selectedSize as any).value,
      symbol: (selectedSymbol as any).value,
    };
    axios.defaults.baseURL = "http://localhost:23336";
    axios.defaults.headers.post["Content-Type"] =
      "application/json;charset=utf-8";
    axios.defaults.headers.post["Access-Control-Allow-Origin"] = "*";
    axios.post("http://localhost:23336", data).catch((err) => {
      console.log(err);
    });
  };

  const setBuy = () => {
    setSelectedOption({ value: "LONG", label: "BUY" });
  };

  const setSell = () => {
    setSelectedOption({ value: "SHORT", label: "SELL" });
  };

  return (
    <div className="App">
      <div>
        <h1>Entry Type: "{selectedOption.value && selectedOption.value}"</h1>
        <h1>Entry Size: "{selectedSize.value}"</h1>
        <h1>Entry Symbol: "{selectedSymbol.value}"</h1>
        <>
          <Button variant="success" onClick={setBuy}>
            BUY
          </Button>{" "}
          <Button variant="danger" onClick={setSell}>SELL</Button>{" "}
        </>
        <div style={{ marginTop: "10px" }}>
          <CreatableSelect
            isClearable
            placeholder="Entry Size"
            onChange={handleChangeSize}
            options={sizeOptions}
          />
        </div>
        <div style={{ marginTop: "10px" }}>
          <CreatableSelect
            isClearable
            placeholder="Entry Ticker"
            onChange={handleChangeSymbol}
            options={symbolOptions}
          />
        </div>
        <button style={{ marginTop: "15px" }} onClick={submitFunction}>
          Submit
        </button>
      </div>
    </div>
  );
}

export default App;
